import Vue from 'vue'
    export default {
        data () {
           return {
               newsList:[],
           }
        },
        created : function () {
               this.$axios.post("http://www.ssjh.com/m/getAppToutiao").then(res=>{
                    console.log(111,res.data.data.list);
                    this.newsList=res.data.data.list;
                    
               })
       },
       methods : {
           toNewsDetail (url) {
               location.href=url;
           }
       }
    }



